
import {
    getCurrentLanguage,
    MultiLanguage,
    MultiLanguageChange
} from '../Infra/Language'

import {
    Endpoints,
    authenticate_Input,
    authenticate_Output
} from '../Infra/Endpoints'

import {
    postPublicPortal,
    SetLanguageHTMLSelect,
    IsLoading,
    loadingOn,
    loadingOff,
    loginOk,
    fadeOut,
    setToStorage,
    displaySystemPopup,
} from '../Infra/Util'

import MyForm from '../Components/Forms/Login'
import PasswordField from '../Components/Fields/Password'

export default class {

    getHtml() { return MyForm.getHtml(); }

    constructor(params) {
        this.params = params;

        // -----------------------------------------------------------------------------
        // page events here --------------------------------------------------------
        // -----------------------------------------------------------------------------

        $(document).ready(function () {
            SetLanguageHTMLSelect()
        });

        $(document).on('keydown', function (e) {
            switch (e.keyCode) {
                case 9: MyForm.validate({ focus: false, msg: false, fields: null }); break;
                case 13: btnSubmit_Click(); break;
            }
        });

        $(document).on('change', '#languageSel', function () {
            if (MultiLanguageChange($('#languageSel').val()))
                setTimeout(() => { location.href = '/login'; }, 100);
        });

        document.body.addEventListener("click", e => {            
            if ($(e.target.parentElement)[0].id == 'popupClose') {
                fadeOut ($('#popUpSystem')[0], 120);
                return;
            }
            var elements = MyForm.elements();
            switch ($(e.target).attr('id')) {
                case elements.btnSubmit: btnSubmit_Click(); break;
                case 'seePass' + elements.formPass: PasswordField.btnSeePassword(elements.formPass); break;
            }
        });

        function btnSubmit_Click() {
            if (IsLoading()) return;
            var elements = MyForm.elements();
            var formData = authenticate_Input(
                $('#' + elements.formMail).val().trim(),
                $('#' + elements.formPass).val().trim(),
                getCurrentLanguage(),
            );

            if (!MyForm.validate({
                focus: false,
                msg: true,
                fields: formData
            }))
                return;

            loadingOn('#' + elements.btnSubmit)
            backend(formData)
        }

        // ----------------------------------------------------------------------------
        // backend access here --------------------------------------------------------
        // ----------------------------------------------------------------------------

        function backend(formData) {
            postPublicPortal( Endpoints().authenticate, formData)
                .then(resp => {
                    if (resp.ok == true)
                        serviceOk(resp.payload)     // output service data 
                    else
                        displaySystemPopup(MultiLanguage(5), resp.msg);
                })
                .catch(resp => {
                    displaySystemPopup(MultiLanguage(5), resp.msg)
                });
        }

        // -----------------------------------------------------------------------------
        // Flow and routing decision here ----------------------------------------------
        // -----------------------------------------------------------------------------

        function serviceOk(payload) {
            loadingOff()
            var response = authenticate_Output(payload);
            loginOk(response);
            if ($('#' + MyForm.elements().keepLogged).is(':checked'))
                setToStorage('hsh', response.user.hsh);
            location.href = '/';
        }
    }
}
