
import { MultiLanguage } from '../../Infra/Language'

import {
    isFieldContentValid,
    errorField,
    displaySystemPopup,
    errorClean
} from '../../Infra/Util'

export default class {

    static getHtml(id, msg, title) {
        return `<div class="form-row no-padding">
                <i class="fa fa-lock" style='padding-left:4px' id='fail${id}'></i>
                <table width='100%'>
                    <tr>
                        <td valign='top' width='90%'>
                            <input id="${id}" type="password" class="form-element" placeholder="${msg}">
                        </td>
                        <td valign='top'>
                            <i class="fa fa-eye" id='seePass${id}' title='${title}'></i>
                        </td>
                    </tr>
                </table>
            </div>`;
    }

    static btnSeePassword(id) {
        $(id).removeAttr('type')
        if ($('#seePass' + id).css('color') === $('body').css('color')) {
            $('#seePass' + id).css('color', 'red')
            $('#' + id).attr('type', 'text')
        }
        else {
            $('#seePass' + id).css('color', $('body').css('color'))
            $('#' + id).attr('type', 'password')
        }
        $('#' + id)[0].focus()
    }

    static validate(id, _params) {
        if (!isFieldContentValid($('#' + id).val(), 20, 'password', 4)) {
            if (_params.focus == true)
                $('#' + id).focus()
            else {
                errorField('#fail' + id)
                if (_params.msg == true)
                    displaySystemPopup(MultiLanguage(5), MultiLanguage(9))
            }
            return false;
        }
        else
            errorClean('#fail' + id)

        return true;
    }
}
