
export default class {
    static getHtml(id, str, checked, _style) {
        if (_style == undefined) _style =''; else _style = "style=" + _style;
        return `<div class="form-row no-padding">
            <input type='checkbox' id='${id}' ${checked} /><span ${_style}>${str}</span>
        </div>`;
    }
}
