
import { MultiLanguage } from '../../Infra/Language'

import EmailField from '../Fields/Email'
import PasswordField from '../Fields/Password'
import CheckBoxField from '../Fields/CheckBox'
import Label from '../Label'
import Popup from '../Popup'
import LanguageSelect from '../LanguageSelect'

export default class {

    // here comes all interactable elements Id´s 
    static elements() {
        return {
            formMail: 'formMail',
            formPass: 'formPass',
            keepLogged: 'keepLogged',
            btnSubmit: 'btnSubmit',
        };
    }

    static getHtml() {

        var elements = this.elements();

        var checkMsg = MultiLanguage(11)
        var forgotMsg = MultiLanguage(0)
        var loginMsg = MultiLanguage(1)
        var ncadMsg = MultiLanguage(2)
        var cadHereMsg = MultiLanguage(3)
        var placeholderPass =  MultiLanguage(7)
        var placeholderPass_Title =  MultiLanguage(8)

        return `<div style="width:296px" class="form-row-group-dark"> 
            <br>${Popup.getHtml()}
            <table><tr><td width='120px'><b>Login</b></td><td width='50%'>${LanguageSelect.getHtml()}</td></tr></table>
            <br>            
            <div class="form-row-group with-icons" align="left">                    
                ${EmailField.getHtml(elements.formMail)}
                ${PasswordField.getHtml(elements.formPass, placeholderPass, placeholderPass_Title)}
                <br>
                ${CheckBoxField.getHtml(elements.keepLogged, checkMsg, 'checked', 'color:#c7c7c7;font-size:small')}
            </div>
            <br>                
            <div class="form-row txt-center">
                <a href="/forgot">
                    ${Label.getHtml('span', 'fp', forgotMsg)}
                </a>
            </div>
            <br>
            <div class="form-row">
                <a id="${elements.btnSubmit}" class="button circle block green">
                       ${loginMsg} <i class="fa fa-spinner fa-spin" id='loading' style="display:none;"></i>
                </a>
            </div>
            <br>
            <div class="form-row txt-center">
                ${ncadMsg} <a href="/register">${cadHereMsg}</a>
            </div>
            <br>
        </div>`;
    }

    static validate(_params) {
        var elements = this.elements();

        var valEmail = EmailField.validate(elements.formMail, _params)
        var valPass = PasswordField.validate(elements.formPass, _params)

        if (!(valEmail && valPass))
            return false;

        document.activeElement.blur();  // ok -> loose focus
        return true;
    }    
}
