
import { MultiLanguage } from '../../Infra/Language'

import {    isFieldContentValid, 
            errorField, 
            displaySystemPopup, 
            errorClean } from '../../Infra/Util'

export default class {
    
    static getHtml(id,  msg) {

        if (msg == undefined)
            msg = MultiLanguage(4);

        return `<div class="form-row no-padding">
                <i class="fa fa-envelope" id='fail${id}'></i>
                <input id="${id}" type="text" class="form-element" placeholder="${msg}">
            </div>`;
    }

    static validate(id, _params) {
        if (!isFieldContentValid($('#'+id).val(), 99, 'email')) {
            if (_params.focus == true)
                $('#'+ id ).focus()
            else {
                errorField('#fail' + id)
                if (_params.msg == true)
                    displaySystemPopup( MultiLanguage(5), MultiLanguage(6))
            }
            return false;
        }
        else
            errorClean('#fail' + id)
        return true;
    }    
}
